import React, { PureComponent } from 'react';
import {Tappable, Separator} from '@vkontakte/vkui';

export class Card extends PureComponent {

    render() {
        let {Card} = require('@vkontakte/vkui');
        return (
            <Card size="s" style={{ borderRadius: '14px', margin: '16px 6px', background: 'white', boxShadow: '0 0 2px 0 rgba(0,0,0,.08), 0 2px 20px 0 rgba(0,0,0,.06)' }}>
                <div style={{ width: '250px', minHeight: '274px' }}>
                    <div style={{ padding: '20px' }}>
                        {
                            this.props.hasOwnProperty('icon') &&
                            <div style={{ background: 'url("'+this.props.icon+'")', backgroundRepeat: 'no-repeat', backgroundSize: '44px 44px', height: '44px', marginBottom: '8px' }}/>
                        }
                        <div style={{ marginBottom: '9px', fontSize: '20px', lineHeight: '24px', fontWeight: '600' }}>{this.props.title}</div>
                        {
                            this.props.hasOwnProperty('text') &&
                            <div style={{ fontSize: '16px', color: '#818c99', lineHeight: '20px' }}>{this.props.text}</div>
                        }
                        {
                            this.props.hasOwnProperty('button_text') &&
                            <div style={{ margin: '17px -20px -20px' }}>
                                <div style={{ position: 'absolute', bottom: '0', width: '100%' }}>
                                    <Tappable style={{ borderRadius: '0px 0px 14px 14px' }} onClick={this.props.onClick}>
                                        <Separator wide/>
                                        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', color: '#3f8ae0' }}>
                                            { this.props.button_icon }
                                            <p style={{ marginLeft: 8 }}>{this.props.button_text}</p>
                                        </div>
                                    </Tappable>
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </Card>
        )
    }

}